@import '../../css/px2rem.scss';
.perfect-container {
    width: 100%;
    height: 100%;
    position: relative;
}
.perfect-content {
    width: 500px;
    background-color: #fff;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 4px;
    box-shadow: 0px 0px 10px 2px #ccc;
    padding: 30px 50px;
}
.perfect-title {
    text-align: center;
    h3 {
        font-size: 25px;
    }
}
.perfect-form {
    width: 100%;
    margin-top: 32px;
}
.perfect-form .perfect-input {
    border: 1px solid #ccc;
    border-radius: 2px;
    position: relative;
    margin-top: 40px;
    .form-label {
        position: absolute;
        top: -30px;
        height: 35px;
        line-height: 35px;
        font-size: 12px;
    }
}
.perfect-form .perfect-input input {
    width: 100%;
    height: 35px;
    padding-left: 8px;
    border: 0;
    font-size: 12px;
}

.perfect-form .perfect-input:nth-child(1) {
    margin-top: 0;
}
.perfect-button {
    
    display: flex;
    justify-content: space-between;
    div {
        width:45%;
        background-color: #3E82FB;
        color: #fff;
        height: 35px;
        margin-top: 32px;
        border-radius: 4px;
        text-align: center;
        line-height: 35px;
        cursor: pointer;
        font-size: 12px;
    }
}

@media screen and (max-width: 425px) {
    .perfect-content {
        width: 80%;
        padding: px2rem(50) px2rem(50)
    }
}

